/* Modern Base Styles with Gradient Background */
.ad-creator-container {
  max-width: 900px;
  margin: 2rem auto;
  padding: 3rem;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  color: #2d3748;
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  position: relative;
  overflow: hidden;
}

/* Decorative Elements */
.ad-creator-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8px;
  background: linear-gradient(90deg, #4f46e5, #10b981);
}

/* Typography Enhancements */
.ad-creator-container h1 {
  color: #4f46e5;
  font-size: 2.75rem;
  text-align: center;
  margin-bottom: 0.25rem;
  font-weight: 800;
  letter-spacing: -0.5px;
  background: linear-gradient(90deg, #4f46e5, #7c3aed);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.ad-creator-container h2 {
  color: #1f2937;
  font-size: 1.75rem;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  position: relative;
  display: inline-block;
  width: 100%;
}

.ad-creator-container h2::after {
  content: '';
  display: block;
  width: 80px;
  height: 4px;
  background: linear-gradient(90deg, #4f46e5, #10b981);
  margin: 0.5rem auto 0;
  border-radius: 2px;
}

.ad-creator-container h3 {
  color: #374151;
  font-size: 1.3rem;
  margin-bottom: 1.25rem;
  font-weight: 600;
  display: flex;
  align-items: center;
}

.ad-creator-container h3::before {
  content: '';
  display: inline-block;
  width: 8px;
  height: 8px;
  background: #4f46e5;
  border-radius: 50%;
  margin-right: 12px;
}

/* Enhanced Error Message */
.error-message {
  background-color: #fff5f5;
  color: #dc2626;
  padding: 1.25rem;
  border-radius: 10px;
  margin-bottom: 2rem;
  border-left: 5px solid #dc2626;
  font-weight: 500;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 8px rgba(220, 38, 38, 0.1);
}

.error-message::before {
  content: '!';
  display: inline-block;
  width: 24px;
  height: 24px;
  background: #dc2626;
  color: white;
  border-radius: 50%;
  text-align: center;
  line-height: 24px;
  margin-right: 12px;
  font-weight: bold;
}

/* Card-like Form Sections */
.product-input-form,
.content-selection,
.campaign-preview {
  background: white;
  padding: 2.5rem;
  border-radius: 14px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  margin-top: 2rem;
  border: 1px solid #f0f0f0;
  position: relative;
  overflow: hidden;
}

/* Form Group Enhancements */
.form-group {
  margin-bottom: 2rem;
  display: flex;
  flex-wrap: wrap;
  position: relative;
}

.form-group label {
  display: block;
  margin-bottom: 0.75rem;
  font-weight: 600;
  color: #4b5563;
  font-size: 0.95rem;
  letter-spacing: 0.2px;
}

.form-group input[type="text"],
.form-group input[type="url"],
.form-group textarea {
  width: 100%;
  padding: 1rem 1.25rem;
  border: 1px solid #e2e8f0;
  border-radius: 10px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #f8fafc;
}

.form-group input[type="text"]:focus,
.form-group input[type="url"]:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #4f46e5;
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.15);
  background: white;
}

.form-group textarea {
  min-height: 140px;
  resize: vertical;
  line-height: 1.6;
}

/* Floating Label Animation (optional) */
/* .form-group input:focus + label, 
.form-group textarea:focus + label {
  transform: translateY(-120%);
  font-size: 0.8rem;
} */

/* Button Enhancements */
.generate-btn,
.confirm-btn,
.reset-btn {
  display: block;
  width: 100%;
  padding: 1.1rem;
  border: none;
  border-radius: 10px;
  font-size: 1.05rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1.5rem;
  letter-spacing: 0.3px;
  position: relative;
  overflow: hidden;
}

.generate-btn {
  background: linear-gradient(135deg, #4f46e5, #7c3aed);
  color: white;
  box-shadow: 0 4px 12px rgba(79, 70, 229, 0.25);
}

.generate-btn:hover {
  background: linear-gradient(135deg, #4338ca, #6d28d9);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(79, 70, 229, 0.3);
}

.generate-btn:active {
  transform: translateY(0);
}

.generate-btn:disabled {
  background: #c7d2fe;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.confirm-btn {
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.25);
}

.confirm-btn:hover {
  background: linear-gradient(135deg, #0d9f6e, #047857);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(16, 185, 129, 0.3);
}

.confirm-btn:disabled {
  background: #a7f3d0;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.reset-btn {
  background: white;
  color: #4f46e5;
  border: 2px solid #e0e7ff;
  font-weight: 600;
}

.reset-btn:hover {
  background: #f0f5ff;
  transform: translateY(-2px);
  border-color: #c7d2fe;
}

/* Content Selection Enhancements */
.content-section {
  margin-bottom: 2.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #f0f0f0;
}

.content-section:last-child {
  border-bottom: none;
}

.content-option {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1rem;
  padding: 1.25rem;
  border-radius: 10px;
  background-color: #f8fafc;
  transition: all 0.3s ease;
  border: 1px solid #e2e8f0;
}

.content-option:hover {
  background-color: #f0f5ff;
  border-color: #c7d2fe;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(79, 70, 229, 0.1);
}

.content-option input[type="radio"] {
  margin-right: 1.25rem;
  margin-top: 2px;
  min-width: 18px;
  min-height: 18px;
  accent-color: #4f46e5;
}

.content-option label {
  flex: 1;
  cursor: pointer;
  line-height: 1.6;
  color: #374151;
}

/* Campaign Preview Enhancements */
.campaign-preview {
  text-align: center;
  padding: 3rem;
}

.final-image {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
  margin-bottom: 2rem;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  border: 1px solid #f0f0f0;
  max-height: 400px;
  object-fit: contain;
}

.campaign-content h3 {
  font-size: 1.75rem;
  color: #1e293b;
  margin-bottom: 1rem;
  font-weight: 700;
  letter-spacing: -0.25px;
}

.slogan {
  color: #4f46e5;
  font-style: italic;
  font-weight: 500;
  margin-bottom: 1.5rem;
  font-size: 1.25rem;
  position: relative;
  display: inline-block;
}

.slogan::before,
.slogan::after {
  content: '"';
  color: #a5b4fc;
  font-size: 1.5rem;
}

.description {
  color: #4b5563;
  line-height: 1.8;
  margin-bottom: 3rem;
  font-size: 1.1rem;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

/* Step Indicator (New) */
.step-indicator {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  position: relative;
}

.step {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: #e2e8f0;
  color: #64748b;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  margin: 0 1rem;
  position: relative;
  z-index: 2;
}

.step.active {
  background: linear-gradient(135deg, #4f46e5, #7c3aed);
  color: white;
}

.step.completed {
  background: #10b981;
  color: white;
}

.step-indicator::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 2px;
  background: #e2e8f0;
  z-index: 1;
}

/* Responsive Design */
@media (max-width: 768px) {
  .ad-creator-container {
    padding: 1.5rem;
    margin: 1rem;
    border-radius: 12px;
  }

  .ad-creator-container h1 {
    font-size: 2.25rem;
  }

  .ad-creator-container h2 {
    font-size: 1.5rem;
  }

  .product-input-form,
  .content-selection,
  .campaign-preview {
    padding: .05rem;
  }

  .step-indicator {
    margin-bottom: 1.5rem;
  }

  .step {
    width: 30px;
    height: 30px;
    font-size: 0.9rem;
  }
}

/* Animation for Loading State */
@keyframes pulse {
  0% {
    opacity: 0.6;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0.6;
  }
}

.is-loading {
  animation: pulse 1.5s infinite ease-in-out;
}